.zoehis_vertical_menu{ position: relative;height: 100%; box-sizing: border-box;overflow: auto;
  .c-new{
    position: relative; background-color: $MAINNAV; width: 62px;height: 100%;overflow: auto;  height: calc(100% - 32px);
    .c-new-menu-items{
      overflow: hidden;
      .item_con_ul{padding: 0;margin: 0;
        .c-menu-new{
          width: 62px;  text-align: center; overflow: hidden; background: $MAINNAV; border-bottom: 1px solid $MAINLEFTNAV;
          color: $FONTWHITE; white-space: nowrap; position: relative; cursor: pointer; transition: width 0.3s;display: block;
          &.hovered{background-color: $MAINLEFTNAV;}
          &.selected{
            background-color: $MAINLEFTNAV;
            &:before{
            	content: '';width: 3px;height: 46px;background-color: $MAINPROMPT;position: absolute;left: 0;
            }
            .c-new-menu-all{
              .c-menu-new-num{background:$MAINLEFTNAV; box-shadow: -2px 2px 2px $MAINLEFTNAV;}
            }
          }
          &.active{
            &:before{
              content: '';width: 3px;height: 46px;background-color: $MAINPROMPT;position: absolute;left: 0;
            }
          }
          .c-new-menu-all{
            position: relative; width: 100%; height: 46px;
            .c-menu-new-add{
              position: absolute; left: 3px; top: 3px; color:$MAINHOVER;
            }
            .c-menu-new-num{
              position: absolute; right: 8px; top: 6px; color: $MAINPROMPT; line-height: 10px;
              display: block; border-radius: 75%; background: $MAINNAV; z-index: 5; box-shadow: -2px 2px 2px $MAINNAV;

            }
            .c-menu-new-icon{
              display: block; padding-top: 8px;
            }
            .c-menu-new-name{
              display: block; overflow: hidden; margin: 0 3px;font-size: $ICONFONTMIN;
            }
            .com_first_menu_toggle{display: none;}
          }
        }
      }
    }
    //固定时
    &.com_fix{
      width: 166px;
      .c-new-menu-items{
        .c-menu-new{
          width: 166px;
          &.selected{background-color: $MAINLEFTNAV;}
          .c-new-menu-all{
            line-height: 46px; text-align: left;
            .c-menu-new-icon{font-size: $FONTTIMEICON; position: absolute;
              width: 20px; height: 20px; line-height: 20px;
              padding: 0; top: 50%; margin-top: -11px; left: 18px;}
            .c-menu-new-name{font-size: $FONTINSTRUCTIONS; padding-left: 48px; margin-right: 31px;}
            .com_first_menu_toggle{position: absolute; font-size: $ICONFONTMIN; color: $MAINBG; right: 12px; top: 0;display: block;}
          }
          .com_ordinary_second_box{
            text-align: left;padding: 0;margin: 0;background: $MAINLEFTNAV;
            .com_ordinary_second_item{
              padding-left: 30px; font-size: $ICONFONTNORMAL; color: $MAINBG; height: 46px; line-height: 46px; border-bottom: 1px solid $MAINLEFTNAV;
              text-overflow: ellipsis; white-space: nowrap; overflow: hidden; position: relative;
              &.selected{color: $MAINPROMPT; font-weight: bold;}
            }
          }
        }
      }
    }
  }

  //切换按钮
  .com_ordinary_toggle{
    background: $MAINCOLOR; height: 32px; position: absolute; bottom: 0; left: 0; right: 0; font-size: $FONTMIDDLE;
    color: $FONTWHITE; text-align: center; line-height: 32px; cursor: pointer;overflow: hidden;width: 166px;
    &:hover{background:$MAINLEFTNAV; }
    .com_fix{
      display: inline-block;
      -webkit-transform: rotate(90deg);
      -moz-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
      -o-transform: rotate(90deg);
      transform: rotate(90deg);
    }
    &.comHam{width: 62px;}
  }

  .c-ff-left-second-menu{
    position: absolute;top: 0;left: 10px;width: 140px;z-index: 10;  transition: left 0.5s;
    box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.26);max-height: 100%;overflow: auto;
    &.slide{
      left: 62px;
    }
    .c-second-menu-items{
      line-height: 40px;background-color: $MAINLEFTNAV;
      .c-second-menu-ul{margin: 0;padding: 0;
        .c-second-menu-item{
          width:140px;text-align: left;color: $FONTWHITE; cursor: pointer;overflow: hidden; height: 40px;
          padding-left: 15px;border-bottom: 1px solid $MAINLEFTNAV;box-sizing: border-box; position: relative;
          span{white-space: normal;text-overflow: ellipsis;overflow: hidden; height: 40px;display: block; position: absolute;
            top: 0;  -webkit-box-orient: vertical;display: -webkit-box;word-break: break-all; -webkit-line-clamp: 1;}
          &:hover{
            background-color: $MAINNAV;
          }
          &.selected{
            background-color: $MAINNAV;
          }
        }
      }
    }
  }

  .c-new::-webkit-scrollbar ,
  .c-ff-left-second-menu::-webkit-scrollbar{
    width: 0px;
  }
}




