.zoehis_multiple_select{
  vertical-align: middle; position: relative;
  display: inline-block;
  height: 24px; line-height: 24px;

  .zoehis_multiple_close{
    font-size: $FONTINSTRUCTIONS; color: $MAINCOLOR; display: none; top: 0;
    position: absolute; right: 0; cursor: pointer;
  }
  &:hover .zoehis_multiple_close{display: block;}
  &:focus{outline: none;}
  &.zoehis_multiple_disabled .zoehis_multiple_input_box{border-bottom: none;}
  &.zoehis_multiple_disabled .zoehis_multiple_line:before{content: "-"; position: absolute; left: 9px; top: 50%; line-height: 1px;}
}

.zoehis_multiple_input_box{
  padding: 0 13px 0 5px;
  overflow: hidden; border-bottom: 1px solid $MAINBORDERLINES; box-sizing: border-box; height: 100%;
  .zoehis_multiple_input_item{float: left; border-radius: 2px; font-size: $FONTMAINS;
    &.selected{background: $SELECTHOVER;}
    &:focus{outline: none;}
  }
  .multiple_focus{padding: 0; margin: 0; width: 13px;border: none; background: transparent;
  }

  //没有选中时的输入框
  .zoehis_multiple_input_none{
    width: 100%; border: none; margin: 0; font-size: $FONTMAINS;padding: 0;
    &::-webkit-input-placeholder {color: $MAINNOEDITOR;}
    &:-moz-placeholder {color: $MAINNOEDITOR;}
    &:-ms-input-placeholder {color: $MAINNOEDITOR;}
    &:focus{outline: none;}
    &:disabled{
      background-color: transparent;
    }
  }
  &:focus{outline: none;}


}
.zoehis_multiple_select_dropdown{
  box-shadow: $SHADOWVALUE; background-color: $MAINBG; margin: 3px 0;
  .zoehis_select_dropdown__item.zoehis_selected{color: $MAINCOLOR; background-color: inherit;}
  .zoehis_select_dropdown__item.zoehis_hover{background-color: $MAINHOVER;}
  .zoehis_select_dropdown__item .zoehis_multiple_check{color: $MAINCOLOR;}
  &:focus{outline: none;}
}



