.zoehis_slider{
  &:before,&:after{content: ""; display: table;}
  &::after{clear: both;}
  .zoehis_slider__runway{
    width: 100%; height: 6px; margin: 16px 0; background-color: $MENUAIDE;
    border-radius: 3px; position: relative; cursor: pointer;
    &.show_input{
      margin-right: 69px;
      width: auto;
    }
    &.disabled{
      cursor: default;
      .zoehis_slider__bar{
        background-color: $MAINNOEDITOR;
      }
    }
  }
  .zoehis_slider__button_wrapper{
    height: 36px;
    width: 36px;
    position: absolute;
    z-index: 1001;
    top: -15px;
    transform: translateX(-50%);
    background-color: transparent;
    text-align: center;
    user-select: none;
    line-height: normal;
    &:focus{outline: none;}
    &:after{
      display: inline-block;
      vertical-align: middle;
      content: ""; height: 100%; width: 0;
    }
  }

  .zoehis_slider__button{
    width: 18px;
    height: 18px;
    // border: 2px solid $FONTFOCUS;
    background-color: $MAINBG;
    border-radius: 50%;
    transition: .2s;
    user-select: none;
    vertical-align: middle;
    display: inline-block;
    box-shadow: 0px 1px 6px 0px $MAINSHADOW;
  }
  .zoehis_slider__bar {
    height: 6px;
    background-color: $FONTFOCUS;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    position: absolute
  }
  .zoehis_slider__input{
    float: right;
    margin-top: 8px;
    width: 55px;
  }

  //竖向
  &.is_vertical{
    position: relative; 
    width: 46px; 
    padding-bottom: 30px;
    height: 100%;
    box-sizing: border-box;
    .zoehis_slider__runway{
      width: 6px;
      margin: 0 auto;
      height: 100%;
    }
    .zoehis_slider__button_wrapper{
      top: auto;
      left: -15px;
      transform: translateY(50%);
    }
    .zoehis_slider__bar{
      width: 6px;
      height: auto;
      border-radius: 0 0 3px 3px;
    }

    &.zoehis_slider__with_input{
      .zoehis_slider__input{
        position: absolute;
        bottom: 0;
        margin: 0;
        text-align: center;
        width: 100%;
      }
    }
  }

}


