.zoehis_table{
  position: relative; width: 100%; height: 100%; box-sizing: border-box; border-top: 1px solid $MAINBORDERLINES;

  .com_table__main{width: 100%; height: 100%; position: relative; box-sizing: border-box; border-bottom: 1px solid $MAINBORDERLINES;}


  //头部
  .com_table__header{
    position: relative; width: 100%; overflow: hidden;
  }

  //内容
  .com_table__body{
    position: relative; width: 100%; height: calc(100% - 39px);
    //.com_table__body-inner{padding-bottom: 12px;}
    .zoehis_scrollbar__bar{z-index: 100; }
    .zoehis_scrollbar__wrap{height: calc(100% + 17px);}
  }

  //固定列
  .com_table__fixed{
    position: absolute; top: 0; width: 100px; overflow: hidden; background: $MAINBG; height: 100%;
    .com_table__fixed-header,.com_table__fixed-body{overflow: hidden; position: absolute;top: 0;}
    &.com_table__fixed-left{left: 0; box-shadow: 8px 0 8px -8px $MAINBORDERLINES;
      .com_table__fixed-header{left: 0; }
      .com_table__fixed-body{left: 0; top: 39px; height: calc(100% - 39px);}
    }
    &.com_table__fixed-right{right: 0; box-shadow: -8px 0 8px -8px $MAINBORDERLINES;
      .com_table__fixed-header{right: 0;}
      .com_table__fixed-body{right: 0; top: 39px; height: calc(100% - 39px);}
    }
  }
  .main_data_none{
    position: absolute; text-align: center; width: 100%;
    top: 39px; bottom: 1px; background: $MAINBG;
  }
  //无横向滚动条
  &.com_scrolling_none{
    .com_table__fixed-left{box-shadow: none;}
    .com_table__fixed-right{box-shadow: none;
      .com_table__fixed-body-inner td:first-child{border-left: 1px solid $MAINBORDERLINES;}
    }
  }
  //滚动条到最左边
  &.com_scrolling_left{
    .com_table__fixed-left{box-shadow: none;}
  }
  //滚动条到最右边
  &.com_scrolling_right{
    .com_table__fixed-right{box-shadow: none;
      .com_table__fixed-body-inner td:first-child{border-left: 1px solid $MAINBORDERLINES;}
    }
  }
  //固定列鼠标移上去
  &.fixed_hover .com_table__body .zoehis_scrollbar__bar {opacity: 1; z-index: 100;}
  //若有固定列 则滚动条样式z-index重写
  &.fixed_has .zoehis_scrollbar:active>.zoehis_scrollbar__bar.zoehis_scrollbar__bar_show, .zoehis_scrollbar:focus>.zoehis_scrollbar__bar.zoehis_scrollbar__bar_show, .zoehis_scrollbar:hover>.zoehis_scrollbar__bar.zoehis_scrollbar__bar_show{z-index: 100;}
  &.page_flag{
    .com_table__main{height: calc(100% - 42px); border-bottom: none;}
    .main_data_none{bottom: 35px;}
  }

  //两行的头部
  &.two_row_th{
    .zoehis_table_col_list{top: 14px;}
    .zoehis_table_box > thead th{height: 24px;}
    .com_table__main .com_table__fixed-body{height: calc(100% - 49px); top: 49px;}
    .com_table__body{height: calc(100% - 49px);}
    .main_data_none{top: 49px;}
  }

  //表格排序公共样式
  .com_sort_box{
    display: inline-block; width: 16px; height: 22px; vertical-align: middle; position: relative;
    .sort_item{
      position: absolute; left: 1px; border: 6px solid transparent; cursor: pointer;
      &.sort_up{top: -3px; border-bottom-color: $MAINNOEDITOR;}
      &.sort_down{bottom: -1px; border-top-color: $MAINNOEDITOR;}
      &.sort_up.active{border-bottom-color: $MAINCOLOR;}
      &.sort_down.active{border-top-color: $MAINCOLOR;}
    }
  }
  .zoehis_table_col_list{
    position: absolute; top:10px; right: 8px; z-index: 1;
    .zoehis_table_dropdown_icon{
      font-size: $ICONFONTMIN; display: inline-block; padding: 3px 3px 2px;
      border-radius: 3px; background-color: $MAINCOLOR; font-weight: bold;
      color: $FONTWHITE;
    }
  }
  .zoehis_table_hidden_col{
    cursor: pointer;
    color: $FONTAUXILIARY; vertical-align: middle; font-size: $FONTTIMEICON; display: inline-block; width: 16px; height: 22px;
    &.zoehis_col_two{vertical-align: inherit;}
  }

  .main_data_none{text-align: center; color: $MAINURGENT; line-height: 65px;}
  .zoehis_table_box{
    table-layout: fixed; width: 100%; border-collapse: collapse; border-spacing: 0;
    &>thead{border-bottom: $border;background: $MAINAUXILIARY;color: $FONTMAIN;
      th{box-sizing: border-box;height: 38px;word-break: break-all;vertical-align: middle;font-weight: bold;font-size: $FONTMAINS;border-right: 1px solid $MENUAIDE;text-align: center;background-color: $MENUAIDE;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        &:last-child{border-right: none;}
      }
    }
    &>tbody>tr{color: $FONTMAIN;cursor: pointer;
      &:hover,&.tr_hover{background-color:$GRIDOVER }
      &.tr_selected{background-color: $MAINHOVER;}
      &:first-child>td{border-top: none;}
      &>td{box-sizing: border-box;height: 38px;word-break: break-all;vertical-align: middle;font-size: $FONTMAINS;border-bottom: $border; border-right: $border;text-align: center;  white-space: nowrap;  overflow: hidden;  text-overflow: ellipsis;color: $FONTMAIN;;
        &.zoehis_table_operator{ //表格操作去样式
          text-align: left;
          &>i{
            color: $MAINCOLOR; font-size: $FONTTIMEICON; margin-left: 8px;
            &:first-child{margin-left: 11px;}
          }
          .disabled{color: $FONTAUXILIARY; pointer-events: none;}
          .delete_btn{color: $MAINURGENT}
        }
        .required{
          position: relative;
          &:before{
            left: -2px;
          }
        }
        &:first-child{border-left: none;}
        &:last-child{border-right: none;}
        //模拟组件的样式，居中，左对齐的，右对齐的,主要用于优化性能
        &.common_td_left{padding: 0 5px; box-sizing: border-box;text-align: left;}
        &.common_td_center{padding: 0 5px; box-sizing: border-box;}
        &.common_td_right{padding: 0 5px; box-sizing: border-box;text-align: right;}
      }
    }
    &.td_padding>tbody>tr>td{padding: 0 5px; box-sizing: border-box;}
  }

  .zoehis_table_resize{
    position: absolute; left: 200px; top: 0; bottom: 0; width: 0;
    border-left: 1px solid $MAINBORDERLINES; z-index: 10;
  }
  .zoehis_table_page_box{
    padding: 3px 0;
    .zoehis_page_size, .zoehis_page_total, .zoehis_page_current{font-size: $FONTMAINS;}
    .zoehis_page_current .zoehis_page_current_wrap input{font-size: $FONTMAINS;}
  }
  // 已选条数
  .zoehis_table_select_box{
    position: absolute;
    left: 6px;
    bottom: 12px;
    strong{color: $MAINCOLOR;}
  }
}
.zoehis_table_filter_dlg{
  .filter_inner{position: relative; height: 100%;
    .left_wrap{ width: 295px; height: 100%;border-right: 2px solid $ERRORCOLOR; float: left; box-sizing: border-box;
      .order_ul{list-style: none;padding: 0 16px;margin-bottom: 6px;
        li{height: 39px; line-height: 39px;
          .zoehis_select:nth-child(1){margin-right: 15px;  }
        }
      }
      .footer_btn{padding: 0 16px;  }
    }
    .right_wrap{ float: right; width: calc(100% - 295px); height: 100%; box-sizing: border-box;border-left: 7px solid $MAINMASK;
      .condition_list_main{padding: 10px 27px 0 22px;
        .listItem{height: 39px;line-height: 39px;
          .zoehis_select{margin-right: 15px;}
          .item_sel{ margin-left: 88px;}
          .icon_btn{display: inline-block; margin-left: 8px;
            i{font-size: $FONTTIMEICON;cursor: pointer;}
            i:nth-child(1){color: $MAINCOLOR;}
            i:nth-child(2){color: $MAINURGENT;}
          }
          .seqSpan{position: absolute;color: $MAINPROMPT; font-weight: bold; font-size: $ICONFONTRIGHT;margin: 2px 10px;}
          .rela_sel{margin: 26px 0;}
        }
        .listItem.listBr{  margin-top: 100px;}
        .listItem:nth-child(1){  margin-top: 0;}
      }
      .footer_btn{margin: 6px 0 20px 0;padding: 0 30px 0 22px;}
    }

    .filter_inner_order,.filter_inner_conditions{height: 100%;}
  }
  .zoehis_scrollbar__wrap{height: calc(100% + 17px); width: calc(100% + 17px);}
}
.zoehis_table_drop_item{
  padding: 0 10px 0 8px; cursor: pointer;
}
.zoehis_table_drop_item:hover{
  background-color: $MAINHOVER;
  color: $FONTMAIN;
}





