.zoehis_progress{ line-height: 1; position: relative;
  &.is_success{
    .zoehis_progress_bar__inner{background-color: $MAINPROMPTS;}
    .zoehis_progress__text{color: $MAINPROMPTS;}
  }
  &.is_exception{
    .zoehis_progress_bar__inner{background-color: $MAINURGENT;}
    .zoehis_progress__text{color: $MAINURGENT;}
  }
}
.zoehis_progress_bar{
  padding-right: 55px; display: inline-block; vertical-align: middle;
  width: 100%; margin-right: -60px; box-sizing: border-box;
}
.zoehis_progress_bar__outer{
  height: 12px; border-radius: 2px; background-color: $MAINBORDERLINES;
  overflow: hidden; position: relative; vertical-align: middle;
}
.zoehis_progress_bar__inner{
  position: absolute; left: 0; top: 0; height: 100%;
  background-color: $MAINCOLOR; text-align: right; line-height: 1;
  border-radius: 2px; white-space: nowrap; transition: width 0.6s ease;
  &:after{display: inline-block; content: ""; height: 100%; vertical-align: middle;}
}
.zoehis_progress__text{
  color: $FONTMAIN; display: inline-block; vertical-align: middle;
  margin-left: 8px; line-height: 1;
}
.zoehis_progress_bar__innerText{
  color: $FONTWHITE; display: inline-block; vertical-align: middle; font-size: 12px; margin: 0 5px;
}
.zoehis_progress_text_inside ,.zoehis_progress_without_text{
  .zoehis_progress_bar{padding-right: 0; margin-right: 0;}
}



