.zoehis_date_editor{
  display: inline-block; position: relative; font-size: $FONTMAINS; color: $FONTMAIN; font-family: $FONTNORMAL; height: 24px; line-height: normal;
}
.zoehis_date_editor_box{
  font-size: $FONTMAINS; color: $FONTMAIN; font-family: $FONTNORMAL; outline: none;
  box-shadow: $SHADOWVALUE; margin: 3px 0; box-sizing: border-box; background-color: $MAINBG;
  &:focus{outline: 1px solid $MAINBORDERLINES;}
  .zoehis_date_editor_box_inner{padding: 10px 3px 8px 14px;}
  .zoehis_date_editor_box_btn{text-align: right; padding: 5px 14px 5px 0; border-top: 1px solid $MAINBORDERLINES;}
  .zoehis_date_editor_item{
    display: inline-block;
    &:last-child{margin-right: 10px;}
    .zoehis_date_text{
      display: inline-block; vertical-align: middle; font-weight: bold; color: $FONTRPTITLE; padding: 0 8px; line-height: 24px;
      &:last-child{padding-right: 0;}
    }
  }
  .zoehis_date_editor_item::last-child{margin-right: 10px;}
  .zoehis_date_editor_clear{padding-right: 10px; color: $MAINCOLOR; cursor: pointer;}

}


