.zoehis_menu{
  font-family: $FONTNORMAL; display: inline-block;  line-height: 33px;  padding: 0 14px;
  margin-right: 5px;  cursor: pointer;  border-top: 2px solid transparent;  border-bottom: none;
  &.zoehis_check1{background-color: $MAINAUXILIARY; border-top: 2px solid $MAINPROMPT;  border-left: 1px solid $MAINBORDERLINES;
    border-right: 1px solid $MAINBORDERLINES;}
  &.zoehis_menu_hover{
    &:hover{background-color: $MAINAUXILIARY; border-top: 2px solid $MAINPROMPT;  border-left: 1px solid $MAINBORDERLINES;
      border-right: 1px solid $MAINBORDERLINES;}
  }
  &.zoehis_menu_visible{visibility: hidden;display: none;}
  &.zoehis_menu_disabled{color: $FONTAUXILIARY;}
  &.zoehis_menu_disabled_style2{color: $FONTDIS;}

}
.zoehis_menu_group{    min-width: 950px;
  border-bottom: 1px solid $MAINBORDERLINES; padding: 3px 0px 0 0px; overflow: hidden;
  &.zoehis_menu_group_bg{background-color: $MAINAUXILIARY; padding-left: 10px;padding-right: 10px;}
  &.zoehis_menu_group_style3{padding: 0;}
}
.zoehis_menu_font{
  font-size: $FONTMAINS;
}
.zoehis_menu_group_content{
  overflow: hidden; position: relative; margin-bottom: -1px; float: left;
  white-space: nowrap;
  //&.has_replace{    width: calc(100% - 202px);  min-width: 737px;}
}
.zoehis_selected_con{  float: left;  height: 35px;  width: 125px;  line-height: 35px;  text-align: center;    box-sizing: border-box;}
.zoehis_menu_selectedTxt{    background-color: $GRIDHEADERBG;  border-top: 2px solid $MAINPROMPT;  border-left: 1px solid $MAINBORDERLINES;  border-right: 1px solid $MAINBORDERLINES;}
//超过一行显示下拉框
.zoehis_menu_select{float: left; height: 35px; line-height: 35px;
  .zoehis_select{height: 35px; line-height: 35px;    padding-right: 12px;  box-sizing: border-box;}
  .zoehis_input{ border-bottom: none; height: 100%; line-height: 35px;
    &.zoehis_over{border-bottom: none;}
    .zoehis_input_inner{border-bottom: none; font-size: $FONTMAINS; text-align: right!important;
    &::-webkit-input-placeholder{color: $FONTMAIN;}}
  }
  .zoehis_select_dropdown{width: 125px;}
}
.zoehis_menu_style2{
  display: inline-block;  line-height: 24px;  padding: 0 14px;  margin-right: 5px;  cursor: pointer;  float: left;
  border: 1px solid $FONTWHITE;  border-bottom: none;  font-size: $FONTINSTRUCTIONS;
  &.zoehis_menu_type2{ border-color: $MAINAUXILIARY;}
  &.zoehis_check2{background-color: $FONTWHITE; border: 1px solid $MAINLEVELTBORDER; border-bottom-color: $FONTWHITE; font-weight: bold;}
  &.zoehis_menu_hover{
    &:hover{background-color: $FONTWHITE;border: 1px solid $MAINLEVELTBORDER; border-bottom-color: $FONTWHITE;}
  }
}
.zoehis_menu_style3{
  display: inline-block;  line-height: 33px;  padding: 0 16px;  margin-right: 0;  cursor: pointer;  float: left; border: none;
  color: $FONTMAIN;
  &.zoehis_menu_type3{}
  &.zoehis_check3{margin-bottom: 1px; background-color: $FONTWHITE; border-bottom: 2px solid $FONTFOCUS; color: $FONTFOCUS; font-weight: bold; padding: 0 14px;}
  &.zoehis_menu_style3_hover{
    &:hover{background-color: $FONTWHITE; border-bottom: 2px solid $FONTFOCUS; color: $FONTFOCUS; font-weight: bold;}
  }
}
.isVisibility{visibility: hidden;}



