.zoehis_select{
  display: inline-block; position: relative; font-size: $FONTMAINS; color: $FONTMAIN; font-family: $FONTNORMAL; height: 24px; line-height: normal;
  .zoehis_input .zoehis_input_icon.z_dropD_form{ font-size: $FONTINSTRUCTIONS; color: $INPUTBORDER; }
  .zoehis_over .zoehis_input_icon.z_dropD_form{ color: $MAINCOLOR; }
}
.zoehis_select_dropdown{
  font-size: $FONTMAINS; color: $FONTMAIN; font-family: $FONTNORMAL; outline: none;
  margin: 3px 0; box-sizing: border-box;
  //background-color: $MAINBG; box-shadow: $SHADOWVALUE;
  &.zoehis_multiple{box-shadow: none; margin: 0;}
  .zoehis_select_dropdown__wrap{max-height: 249px;}
  .zoehis_scrollbar__view{
    margin: 0; padding: 0;
  }
  .zoehis_select_dropdown__item{
    &.zoehis_has_icon{ padding-right: 30px }
    margin: 0; list-style: none; height: $SELECTITEMHEIGHT; font-size: $FONTMAINS; padding: 6px 15px 6px 5px; position: relative; line-height: $SELECTITEMHEIGHT - 12;
    white-space: nowrap; overflow: hidden; text-overflow: ellipsis; border-bottom: 1px solid $SELECTITEMBORDER; color: $FONTMAIN;
    box-sizing: border-box; cursor: pointer; float: none;
    .zoehis_select_dropdown_renderItem {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &:hover,&.zoehis_hover,&.detail_hover{background-color: $SELECTHOVER;}
    &.zoehis_selected{color: $FONTWHITE; background-color: $MAINCOLOR;}
    .z_arrowTTR_normal {position: absolute;right: 12px;}
  }
  .zoehis_select_dropdown__empty{
    margin: 0; padding: 0; line-height: 33px; height: 33px;
    text-align: center; background: $MAINAUXILIARY; color: $FONTAUXILIARY; font-weight: normal;
  }
  .noselect_color span{color: $FONTAUXILIARY;}


  .zoehis_select_scroll_inner{height: 100%;}
  .zoehis_select_detail_box{
    width: 418px; float: right;padding-left: 2px;
     font-size: $FONTMAINS;
    .zoehis_select_detail{ background-color: $MAINBG; width: 100%; height: 100%; border: 1px solid $MAINBORDERLINES;box-shadow: $SHADOWVALUE;}
    .zoehis_select_detail__wrap_new{color: $MASKBG;height: 100%;overflow: auto;}
    .zoehis_select_detail__wrap{max-height: 380px;color: $MASKBG;}
    .zoehis_text_tt{color: $MAINURGENT; font-weight: bold;}
    h5{color: $FONTFOCUS; font-weight: bold; padding-left: 15px; font-size: $FONTMAINS; line-height: 45px; margin: 0;
      text-overflow: ellipsis; overflow: hidden; white-space: nowrap;
    }
    p{padding: 0 9px 0 15px; line-height: 24px; margin: 0 0 13px;}
  }
  .zoehis_had_detail{
    //margin-right: 16px;
    background: $MAINBG;
    box-shadow: $SHADOWVALUE;
  }
  //.zoehis_select_shadow{width: 100%; background: $MAINBG; box-shadow: $SHADOWVALUE;
  //  position: absolute; left: 0; top: 0; bottom: 0;
  //}
  .zoehis_select_shadow{width: 100%; background: transparent;
    position: absolute; left: 0; top: 0; bottom: 0;
  }
  &.show_detail{
    .zoehis_select_dropdown__wrap{max-height: 380px;}
  }
  &.has_detail{
    .zoehis_select_shadow{width:calc(100% - 420px); }
  }
}



