.zoehis_input_number{
  position: relative;
  display: inline-block;
  width: 130px;
  line-height: 30px;
  &.is_disabled{
    .zoehis_input_number__inner{
      background-color: #f5f7fa;
      border-color: #e4e7ed;
      color: #c0c4cc;
      cursor: not-allowed;
    }
    .zoehis_input_number__decrease,.zoehis_input_number__increase{
       border-color: #e4e7ed; color: #e4e7ed; cursor: not-allowed;
    }
  }
  &.is_without_controls{
    .zoehis_input_number__inner{padding: 0 8px;}
  }

  &.is_controls_right{
    .zoehis_input_number__decrease,.zoehis_input_number__increase{
      right: 1px;bottom: 1px;top: auto;left: auto;height: 14px;line-height: 14px;
      box-sizing: border-box;border-right: none;border-left: 1px solid #dcdfe6;border-radius: 0 0 4px 0;
    }
    .zoehis_input_number__icon {font-size: 12px;}
    .zoehis_input_number__inner{padding-left: 8px; padding-right: 39px;}
    .zoehis_input_number__increase{bottom: auto; top: 1px; border-bottom: 1px solid #dcdfe6;}
  }

}
.zoehis_input_number__decrease,.zoehis_input_number__increase{position: absolute;z-index: 1;top: 1px;width: 32px;bottom: 1px;text-align: center;background: #f5f7fa;color: #606266;cursor: pointer;font-size: 13px;&.is_disabled{ color:#c0c4cc; cursor: not-allowed;}&:hover:not(.is_disabled){~.zoehis_input_number__inner:not(.is_disabled){border-color: #00A3E7;}}}
.zoehis_input_number__decrease {
  left: 1px;
  border-radius: 4px 0 0 4px;
  border-right: 1px solid #dcdfe6;
}
.zoehis_input_number__increase {
  right: 1px;
  border-radius: 0 4px 4px 0;
  border-left: 1px solid #dcdfe6;
}

.zoehis_input_number__inner{
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #000;
  display: block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: none;
  padding: 0 33px;
  text-align: center;
  width: 100%;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  &:focus{border-color: #00A3E7;}
}



